@import url(https://cdn.jsdelivr.net/gh/orioncactus/pretendard/dist/web/static/pretendard.css);
.selected {
  border-bottom: oldlace 1px solid;
  transition: 0.2s;
}

@keyframes bounce {
  0% {
    transform: scaleX(100%) scaleY(100%);
  }

  25% {
    transform: scaleX(140%) scaleY(85%);
  }

  40% {
    transform: scaleX(100%) scaleY(100%);
  }

  50% {
    transform: scaleX(90%) scaleY(115%);
  }

  60% {
    transform: scaleX(105%) scaleY(94%);
  }

  70% {
    transform: scaleX(96%) scaleY(106%);
  }

  80% {
    transform: scaleX(103%) scaleY(98%);
  }

  90% {
    transform: scaleX(101%) scaleY(99%);
  }

  92% {
    transform: scaleX(100%) scaleY(100%);
  }

  94% {
    transform: scaleX(101%) scaleY(99%);
  }

  96% {
    transform: scaleX(100%) scaleY(100%);
  }

  98% {
    transform: scaleX(101%) scaleY(99%);
  }

  100% {
    transform: scaleX(100%) scaleY(100%);
  }
}

@media screen and (min-width: 600px) {
  .base {
    font-size: 4.4rem;
    font-weight: 700;
    transition: 0.2s ease-in;
  }
}

@media screen and (max-width: 600px) {
  .base {
    font-size: 10vw;
    font-weight: 700;
    transition: 0.2s ease-in;
  }
}

.base:hover {
  color: #08fdd8;
  transition: 0.2s ease-in;
}

.hovering {
  animation: bounce 2s ease !important;
}

@media screen and (max-width: 600px) {
  .mail-container {
    width: 80%;
    margin-top: 2em;
    border-radius: 16px;
    padding: 1em 0 1em 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #353b41;
  }
}

@media screen and (min-width: 600px) {
  .mail-container {
    width: 50%;
    margin-top: 2em;
    border-radius: 16px;
    padding: 1em 0 1em 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #353b41;
  }
}

@media screen and (min-width: 1000px) {
  .mail-container {
    width: 40%;
    margin-top: 2em;
    border-radius: 16px;
    padding: 1em 0 1em 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #353b41;
  }
}

.mail-container label {
  font-size: 2rem;
  font-weight: 600;
  margin-bottom: 6px;
}

.mail-container > div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80%;
  margin-bottom: 16px;
}
.mail-container > div > div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.mail-container input {
  border: none;
  width: 100% !important;
  height: 28px;
  border-radius: 4px;
  text-align: center;
  padding: 4px;
}

.mail-container textarea {
  border: none;
  border-radius: 4px;
  width: calc(100% + 2px) !important;
  height: 14em;
  font-family: "pretendard";
  padding: 4px;
}

.mail-container textarea {
  resize: none;
}

.field {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.mail-send {
  border: none;
  border-radius: 16px;
  cursor: pointer;
  text-align: center;
  width: 128px;
  height: 36px;
  background: slateblue;
  font-weight: 600;
  color: white;
  font-size: 16px;
  letter-spacing: 1px;
}

svg {
  font-size: 6rem;
  margin-bottom: 12px;
}

html,
body {
  background: #353b48;
  height: 100vh;
  color: white;
  margin: 0;
  padding: 0;
  font-family: "pretendard";
}

body {
  overflow-y: scroll;
}

